import React, { useState } from 'react';
import { 
  AppBar, 
  Toolbar, 
  Button, 
  Box, 
  Drawer,
  IconButton,
  useTheme,
  useMediaQuery,
  Divider,
  List,
  ListItem
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import DataObject from '@mui/icons-material/DataObject';
import CodeIcon from '@mui/icons-material/Code';
import { Language, NavBarState } from './Types';
import { textContent } from './textContext';

type NavBarProps = {
  selectedButton: NavBarState;
  setSelectedButton: (value: NavBarState) => void;
  setShowScrapingForm: (value: boolean) => void;
  language: Language;
  setLanguage: (value: Language) => void;
};

const NavBar: React.FC<NavBarProps> = ({
  selectedButton,
  setSelectedButton,
  setShowScrapingForm,
  language,
  setLanguage
}) => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const toggleLanguage = () => {
    setLanguage(language === 'EN' ? 'ES' : 'EN');
  };

  const commonButtonStyles = (selected: boolean) => ({
    color: 'white',
    fontFamily: '"Press Start 2P", cursive',
    fontSize: isMobile ? '14px' : '10px',
    textTransform: 'none',
    justifyContent: 'flex-start',
    width: '100%',
    '&::after': {
      content: '""',
      position: 'absolute',
      bottom: 0,
      left: 0,
      width: '100%',
      height: '2px',
      backgroundColor: selected ? '#62ff32' : 'transparent',
      transition: 'background-color 0.3s ease',
    }
  });

  const drawerContent = (
    <Box
      sx={{
        backgroundColor: 'black',
        height: '100%',
        padding: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 1
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
        <IconButton
          onClick={handleDrawerToggle}
          sx={{ color: '#62ff32' }}
        >
          <MenuIcon />
        </IconButton>
      </Box>
      <Divider sx={{ backgroundColor: '#62ff32', mb: 2 }} />
      
      <List sx={{ flexGrow: 1 }}>
        <ListItem>
          <Button
            onClick={toggleLanguage}
            sx={{
              color: '#62ff32',
              fontFamily: '"Press Start 2P", cursive',
              fontSize: '12px',
              textTransform: 'none',
              border: '1px solid #62ff32',
              borderRadius: '8px',
              width: '100%'
            }}
          >
            {language === 'EN' ? 'ES' : 'EN'}
          </Button>
        </ListItem>
        
        {['web', 'data', 'about'].map((item) => (
          <ListItem key={item} sx={{ py: 1 }}>
            <Button
              onClick={() => {
                setSelectedButton(item as NavBarState);
                setShowScrapingForm(false);
                setMobileOpen(false);
              }}
              sx={commonButtonStyles(selectedButton === item)}
            >
              {textContent[language][`${item}Services` as keyof typeof textContent[typeof language]]}
              {item === 'web' && <CodeIcon sx={{ ml: 1, fontSize: '16px' }} />}
              {item === 'data' && <DataObject sx={{ ml: 1, fontSize: '16px' }} />}
            </Button>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <>
      <AppBar position="static" sx={{ backgroundColor: 'black', boxShadow: 'none' }}>
        <Toolbar>
          <Box sx={{ flexGrow: 1 }}>
            <img
              src="/melting/melting.png"
              alt="Melting Logo"
              style={{ height: isMobile ? '60px' : '80px', objectFit: 'contain' }}
            />
          </Box>

          {isMobile ? (
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="end"
              onClick={handleDrawerToggle}
              sx={{ color: '#62ff32' }}
            >
              <MenuIcon />
            </IconButton>
          ) : (
            <Box sx={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
              <Button
                onClick={toggleLanguage}
                sx={{
                  color: '#62ff32',
                  fontFamily: '"Press Start 2P", cursive',
                  fontSize: '12px',
                  textTransform: 'none',
                  border: '1px solid #62ff32',
                  borderRadius: '8px',
                  padding: '5px 10px',
                  '&:hover': {
                    backgroundColor: 'rgba(98, 255, 50, 0.1)',
                  },
                }}
              >
                {language === 'EN' ? 'ES' : 'EN'}
              </Button>
              
              {['web', 'data', 'about'].map((item) => (
                <Button
                  key={item}
                  onClick={() => {
                    setSelectedButton(item as NavBarState);
                    setShowScrapingForm(false);
                  }}
                  sx={commonButtonStyles(selectedButton === item)}
                >
                  {textContent[language][`${item}Services` as keyof typeof textContent[typeof language]]}
                  {item === 'web' && <CodeIcon sx={{ ml: 1, fontSize: '16px' }} />}
                  {item === 'data' && <DataObject sx={{ ml: 1, fontSize: '16px' }} />}
                </Button>
              ))}
            </Box>
          )}
        </Toolbar>
      </AppBar>

      <Drawer
        anchor="right"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        sx={{
          '& .MuiDrawer-paper': {
            width: 240,
            backgroundColor: 'black',
            borderLeft: '2px solid #62ff32'
          },
        }}
      >
        {drawerContent}
      </Drawer>
    </>
  );
};

export default NavBar;